.App {
  text-align: center;
}

.Markdown {
  text-align: left;
  padding: 0px 5%;
  max-width: 1000px;
  margin: auto;
}

.markdown-img {
  display: block;
  width: auto;
  height: auto;
  max-width: 90%;
  max-height: 50vh;
  margin: auto;
  padding-bottom: 10px;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
